/* project component css */
.Project {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 10px;
}

.projectImgContainer > .webImg,
.projectImgContainer > .mobileImg,
.projectImgContainer > .otherImg {border-radius: 12px;}

/* web, mobile, and other project imgs contain diff. widths and heights */
.projectImgContainer > .webImg {
    width: 600px;
    height: 410px;
}

.projectImgContainer > .mobileImg {
    width: 300px;
    height: 450px;
}

.projectImgContainer > .otherImg {
    width: 350px;
    height: 300px;
}

.projectDescription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
}

.projectDescription > h5 {
    width: auto;
    color: var(--lightblue);
    border-bottom: 3.5px solid var(--cream);
    text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.projectDescription > p {
    width: 40vw;
    color: var(--white);
}

.techStack {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    font-size: 13px;
    font-family: var(--ibm-font);
}

.techStack > li {margin-right: 8px;}

/* for laptop devices */
@media only screen and (max-width: 1024px) {
    .projectDescription > p {width: 55vw;}
}

/* for tablet devices */
@media only screen and (max-width: 768px) {
    .projectDescription > p {width: 80vw;}

    .projectImgContainer > .webImg {
        width: 500px;
        height: 380px;
    }
}

@media only screen and (max-width: 600px) {
    .projectImgContainer > .webImg {
        width: 400px;
        height: 300px;
    }
}

@media only screen and (max-width: 475px) {
    .projectDescription > p {width: 90vw;}

    .projectImgContainer > .webImg {
        width: 350px;
        height: 240px;
    }
}

/* for large and medium mobile devices */
@media only screen and (max-width: 425px) {
    .projectImgContainer > .webImg {
        width: 360px;
        height: 250px;
    }

    .projectImgContainer > .otherImg {
        width: 250px;
        height: 200px;
    }

    .projectViewBtns {
        display: flex;
        width: 100%;
        justify-content: center;
    }
}

/* for small mobile devices */
@media only screen and (max-width: 320px) {
    .projectImgContainer > .webImg {
        width: 305px;
        height: 210px;
    }
}